.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.countdown-container {
  text-align: center;
}

.additional-text {
  font-Weight: bold;
  text-decoration:underline;
  font-size: 24px;
}

.icon-container {
  /* 样式图标容器，可以使用绝对定位将其放置在右上角 */
  position: absolute;
  top: 10px;
  right: 10px;
  /* 其他样式属性 */
}

a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none; /* 去掉下划线 */
  color: inherit; /* 使用继承的颜色 */
}
